import React from 'react'
import { BrowserRouter as Router, Route } from "react-router-dom";
import Property from "../Forms/Property";
import Agent from "../Forms/Agent";
import Result from "../Result";
import ListingBroker from "../Forms/ListingBroker";
import Lenders from "../Forms/Lenders";
import Attorney from '../Forms/Attorney';
import ForSaleByOwner from '../Forms/ForSaleByOwner';
import Mortgage from "../Forms/Mortgage";
import AgentType from "../Forms/AgentType";
import AdditionalInformation from "../Forms/AdditionalInformation";
import Testing from "../Forms/Testing";
import Client from "../Forms/Client";

const index = () => {
    const deploy = process.env.REACT_APP_DEPLOY_TO_GITHUB_PAGES;
    const path = process.env.REACT_APP_BASE_PATH;
    return (
        <Router>
            <Route exact path={deploy ? `${path}` : "/"} component={AgentType} />
            <Route path={deploy ? `${path}/Client/:clientType` : "/Client/:clientType"} component={Client} />
            <Route path={deploy ? `${path}/Property` : "/Property"} component={Property} />
            <Route path={deploy ? `${path}/Agent/:represents` : "/Agent/:represents"} component={Agent} />
            <Route path={deploy ? `${path}/Attorney/:represents` : "/Attorney/:represents"} component={Attorney} />
            <Route path={deploy ? `${path}/FSBO` : "/FSBO"} component={ForSaleByOwner} />
            <Route path={deploy ? `${path}/ListingBroker` : "/ListingBroker"} component={ListingBroker} />
            <Route path={deploy ? `${path}/Lenders` : "/Lenders"} component={Lenders} />
            <Route path={deploy ? `${path}/result` : "/result"} component={Result} />
            <Route path={deploy ? `${path}/Mortgage` : "/Mortgage"} component={Mortgage} />
            <Route path={deploy ? `${path}/AdditionalInformation` : "/AdditionalInformation"} component={AdditionalInformation} />
            <Route path={deploy ? `${path}/Testing` : "/Testing"} component={Testing} />
        </Router>
    )
}

export default index
